nd-color: #59C7DB;}.about-us .dez-tabs .nav-tabs li.active{	border-right: 4px solid #59C7DB;}/* Rgba color */.dez-info-has.bg-primary,.dez-info-has.bg-secondry:hover,.tp-bannertimer.tp-bottom {    background-color: rgba(#59C7DB, 0.9);}/*===================== 	Premery text color =====================*/a,.text-primary,.primary li:before,.breadcrumb-row ul li a,.header-nav .nav > li.active > a,.header-nav .nav > li.current-menu-item > a .header-nav .nav > li .sub-menu li a:hover,.header-nav .nav > li:hover > a,.header-nav .nav > li .mega-menu > li ul a:hover,.header-nav .nav > li .sub-menu li:hover > a,.nav-dark.header-nav .nav > li .sub-menu li:hover > a,.nav-dark.header-nav .nav > li .mega-menu > li ul a:hover,blockquote:before,ol.comment-list li.comment .reply a,footer a:active,footer a:focus,footer a:hover,footer h1 a,footer h2 a,footer h3 a,footer h4 a,footer h5 a,footer h6 a,footer p a,button.scroltop,.testimonial-1 .testimonial-position,.testimonial-4 .testimonial-name:after,.testimonial-2 .testimonial-position,.testimonial-3 .testimonial-position,.testimonial-3 .quote-left:before,.acod-head a:after,.acod-head a,.acod-head a:hover,.acod-head a.collapsed:hover,.dez-tabs .nav-tabs > li > a i,h1 a:hover,h2 a:hover,h3 a:hover,h4 a:hover,h5 a:hover,h6 a:hover,.site-button.outline,.home-footer-2 .site-button-link{    color: #59C7DB;}/*===================== 	Premery border color =====================*/.navbar-toggle,.testimonial-4 .testimonial-pic,.testimonial-4:after,.testimonial-4 [class*="quote-"],button.scroltop,blockquote,.widget_gallery li:hover {    border-color: #59C7DB;}.testimonial-2 .testimonial-text:after{	border-top:15px solid #59C7DB;}/*===================== 	Secondery bg color =====================*/.bg-secondry {    background-color: #59C7DB;}/*===================== 	Secondery text color =====================*/.text-secondry {    color: #59C7DB;}